














import { Component, Mixins, Prop } from 'vue-property-decorator';
import TwoWay from '@/resources/mixins/two-way';

@Component({
    name: 'MyInputSendCode'
})
export default class MyInputSendCode extends Mixins(TwoWay) {
    /* 布尔值，控制显示‘获取按钮’还是‘倒计时’ */
    showButton = true;
    /* 倒计时 计数器 */
    sendTime = 0;
    timer: any = null;

    // 发送触发外部事件
    sendCodeHandle() {
        this.$emit('send-code'); // 触发外部事件
        const TIME_COUNT = 60; //  更改倒计时时间
        if (!this.timer) {
            this.sendTime = TIME_COUNT;
            this.showButton = false;
            this.timer = setInterval(() => {
                if (this.sendTime > 0 && this.sendTime <= TIME_COUNT) {
                    this.sendTime--;
                } else {
                    this.showButton = true;
                    clearInterval(this.timer); // 清除定时器
                    this.timer = null;
                }
            }, 1000);
        }
    }
    // 重置
    reset() {
        this.$nextTick(() => {
            this.showButton = true;
            clearInterval(this.timer); // 清除定时器
            this.timer = null;
        });
    }
}
